          for(var i1=0;i1<node_label.length;i1++){
            //node_label[i1] = encodeURIComponent(node_label[i1]);
            node_label[i1] = this.customGlobal.encrypt(node_label[i1],this.customGlobal.cryptoKey);
          }
          this.$router.push({ name:'device_query',path: '/device_query',
            query: {
              orgid: this.currentSelTreeNodeId ,
              caption:node_label.reverse(),
              id:node_key.reverse(),
              cur_obj:{orgid:this.currentSelTreeNodeId ,caption:this.$refs.org_tree.getCurrentNode().caption,type:this.$refs.org_tree.getCurrentNode().type+""},
              type:node_type.reverse(),
              obj:node_obj.reverse(),
              cb_info:this.cbInfo,
            }
          })
          this.currentSelTreeNodeIds = node_key.reverse();
