    //返回树节点层级caption
    getTreeNodeParentCaption(label,key ,type,obj, node){
      let currentNode = this.$refs.org_tree.getNode(node);
      let len = currentNode.level;
      let loop1 = 1;
      // console.info("level:"+len);
      while(loop1<len){
        if(currentNode.parent!==undefined) {
          label.push(currentNode.parent.label)  ;
          key.push(currentNode.parent.key)  ;

          type.push(currentNode.parent.data.type+"")  ;
          obj.push({id:currentNode.parent.key,caption:currentNode.parent.label,type:currentNode.parent.data.type+""});

          currentNode = this.$refs.org_tree.getNode(currentNode.parent);
          loop1++;
        }
      }
      return {"label":label,"key":key,"type":type,"obj":obj};


    },

